$primary-color: purple;
$secondary-color: rgb(39, 3, 39);

body {
  margin: 0;
  font-family: sans-serif;
}
.header {
  background-color: $primary-color;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  &__title {
    color: white;
    text-transform: uppercase;

    h1 {
      text-align: center;
    }
  }
}
.introduction {
  padding-left: 10px;
  padding-right: 10px;

  &__title {
  }

  &__text {
  }
}
.call {
  background-color: $secondary-color;
  width: 100%;
  height: 50vh;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  &__title {
    color: white;
    text-transform: uppercase;
  }

  &__button {
  }
}
.speakers {
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  &__title {
    text-transform: uppercase;
    text-align: center;
  }

  &__avatar {
    border-radius: 100%;
    margin-top: 30px;
    width: 50%;
  }

  &__text {
    margin-bottom: 30px;

    &__name {
      text-align: center;
    }

    &__talk {
    }

    &__biography {
    }
  }
}
.previous {
  width: 100%;

  &__title {
    text-align: center;
    text-transform: uppercase;
  }

  &__gallery {
    width: 100%;
    height: 50vh;
    background-color: $primary-color;
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: space-between;

    &__button {
      &--previous {
        img {
          transform: rotate(180deg);
          width: 20px;
          height: 20px;
        }
      }

      &--next {
        img {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}
.sponsors {
  width: 100%;
  margin-top: 30px;
  display: flex;
  flex-flow: column;
  align-items: center;

  &__title {
    text-transform: uppercase;
    text-align: center;
  }

  img {
    width: 60%;
    margin-bottom: 10px;
  }
}
.organization {
  width: 100%;
  &__title {
    text-align: center;
    text-transform: uppercase;
  }

  &__carroussel {
    width: 100%;
    height: 50vh;
    background-color: $primary-color;
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: space-between;

    &__button {
      width: 20px;
      height: 20px;

      &--previous {
        img {
          transform: rotate(180deg);
          width: 20px;
          height: 20px;
        }
      }

      &--next {
        img {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}
.footer {
  background-color: $primary-color;
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  &__logo {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-content: center;

    img {
      width: 30vw;
    }
  }

  &__email {
  }
}
